<template>

    <div style="min-width: 1200px">

      <div class="mlist-control ant-list-bordered">
        <h3 style="margin-left: 30px">发布需求</h3>

      </div>

      <a-form
        class="listpage"
        :style="{backgroundImage: 'url(' + img + ')',
          backgroundRepeat:no-repeat,center,top,
          backgroundSize:'100% 100%'
          }"
        :bordered="true"
        :form="form">
        <h3 style="color: gray">请填写下列表单</h3>
        <a-form-item
          label="选择工种"
          :label-col="{lg: {span: 7}, sm: {span: 7}}"
          :wrapper-col="{lg: {span: 10}, sm: {span: 17} }">
          <a-select
            v-decorator="[
            'needsFarmerType',
            {rules: [{ required: true, message: '请选择工种' }]}
          ]"
            placeholder="请选择工种">
            <a-select-option value="钢筋班组">钢筋班组</a-select-option>
            <a-select-option value="木工">木工</a-select-option>
            <a-select-option value="架子工">架子工</a-select-option>
            <a-select-option value="混凝土">混凝土</a-select-option>
            <a-select-option value="砌砖">砌砖</a-select-option>
            <a-select-option value="抹灰">抹灰</a-select-option>
            <a-select-option value="油漆">油漆</a-select-option>
            <a-select-option value="门窗">门窗</a-select-option>
            <a-select-option value="防水">防水</a-select-option>
            <a-select-option value="水电">水电</a-select-option>
            <a-select-option value="其他">其他</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          :label-col="{lg: {span: 7}, sm: {span: 7}}"
          :wrapper-col="{lg: {span: 10}, sm: {span: 17} }"
          label="所需人数"
          :validate-status="peopleNumber.validateStatus">
          <a-input-number
            :min="0"
            :value="peopleNumber.value"
            @change="handlePeopleNumberChange"
            name="needsNum"
            v-decorator="[
            'needsNum',
            {rules: [{ required: true, message: '请输入需要人数' }]}
          ]"
          />人
        </a-form-item>
        <a-form-item
          label="工作地点"
          :labelCol="{lg: {span: 7}, sm: {span: 7}}"
          :wrapperCol="{lg: {span: 10}, sm: {span: 17} }">
          <a-input
            style="width: 20%"
            placeholder="请输入"
            name="province"
            v-decorator="[
            'province',
            {rules: [{ required: true, message: '请输入省份' }]}
          ]"/>省
          <a-input
            style="width: 20%"
            placeholder="请输入"
            name="centre"
            v-decorator="[
            'centre',
            {rules: [{ required: true, message: '请输入市级' }]}
          ]"/>市
          <a-input
            style="width: 20%"
            placeholder="请输入"
            name="local"
            v-decorator="[
            'local',
            {rules: [{ required: true, message: '请输入县级' }]}
          ]"/>县
          <a-input style="width: 25%" placeholder="请输入" />
        </a-form-item>
        <a-form-item
          label="起止日期"
          :labelCol="{lg: {span: 7}, sm: {span: 7}}"
          :wrapperCol="{lg: {span: 10}, sm: {span: 17} }">
          <a-range-picker
            name="buildTime"
            style="width: 100%"
            v-decorator="[
            'buildTime',
            {rules: [{ required: true, message: '请选择起止日期' }]}
          ]"/>
        </a-form-item>
        <a-form-item
          :label-col="{lg: {span: 7}, sm: {span: 7}}"
          :wrapper-col="{lg: {span: 10}, sm: {span: 17} }"
          label="价值包"
          :validate-status="priceNumber.validateStatus">
          <a-input-number
            :min="0"
            :value="priceNumber.value"
            @change="handlePriceNumberChange"
            name="price"
            v-decorator="[
            'price',
            {rules: [{ required: true, message: '请输入价值包' }]}
          ]"
          />元
        </a-form-item>
        <a-form-item
          label="其他要求"
          :labelCol="{lg: {span: 7}, sm: {span: 7}}"
          :wrapperCol="{lg: {span: 10}, sm: {span: 17} }">
          <a-textarea
            rows="4"
            placeholder="身体健康等其他要求"
            v-decorator="[
            'description'
          ]" />
        </a-form-item>
        <a-form-item
          :wrapperCol="{ span: 24 }"
          style="text-align: center">
          <a-button htmlType="submit" type="primary" @click="postDemand">发布需求</a-button>
        </a-form-item>
      </a-form>

    </div>





</template>

<script>
import {getEnterpriseInfoById} from '../../api/manRequest'
import { postDemandRequest } from '@/api/login'

function validatePeopleNumber (number) {
  if (number > 0) {
    return {
      validatePeopleStatus: 'success',
      peopleErrorMsg: null
    }
  }
  return {
    validatePeopleStatus: 'error',
    peopleErrorMsg: '所需人数必须大于1'
  }
}
function validatePrimeNumber (number) {
  if (number > 0) {
    return {
      validatePriceStatus: 'success',
      priceErrorMsg: null
    }
  }
  return {
    validatePriceStatus: 'error',
    priceErrorMsg: '所需人数必须大于1'
  }
}
export default {
  name: 'PostDemand',
  data () {
    return {
      entId: Number(localStorage.getItem('entId')),
      entAuthState:'',
      description: '',
      value: 1,
      peopleNumber: {
        value: 1
      },
      priceNumber: {
        value: 1
      },
      img:require('../../assets/dddb-webEnt/outbg.jpg'),
      // form
      form: this.$form.createForm(this)

    }
  },
  mounted(){
    this.$parent.showMenu=true
  },
  created() {
    console.log(this.entId)
    this.getEnterpriseInfoById()
  },
  methods: {
    postDemand () {
      console.log(this.entAuthState)
      if (this.entAuthState === '已通过') {
      const { form: { validateFields } } = this
      // 先校验，通过表单校验后，才进入下一步
      validateFields((err, values) => {
        if (!err) {
          if (values.description === undefined) {
            values.description = ''
          }
          const demandValue = { ...values }
          const that = this
          postDemandRequest(demandValue)
          .then(function (response) {
            console.log(response)
            if (response.data.result === 25) {
              console.log('ds')
              that.$router.push({ path: '/DemandSucceed' })
            }
            alert('发布成功')
            // this.$router.push('/DemandSucceed')
          })
          .catch(function (error) {
            console.log(error)
          })
        }
      })
      }else {
        this.error()
      }
    },
    handlePriceNumberChange (value) {
      this.priceNumber = {
        ...validatePrimeNumber(value),
        value
      }
    },
    handlePeopleNumberChange (value) {
      this.priceNumber = {
        ...validatePeopleNumber(value),
        value
      }
    },
    // 获取企业信息
    getEnterpriseInfoById(){
      getEnterpriseInfoById(this.entId)
      .then(response => {
        this.entAuthState = response.data.data.authState
        this.error()
      })
      .catch(err => {
        console.log(err);
      })
    },
    error() {
      if (this.entAuthState != '已通过') {
       this.$error({
          title: '企业资质未通过审核',
          content: '只有通过必填资料才能发布用工需求噢,请前往企业资质提交企业资质信息',
        }); 
      }
      },
  }
}
</script>

<style scoped>
  .list-control {
    background: #fff;
    width: 90%;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 20px;
    height: 80px;
    display: flex;
    flex-direction: row;
  }
  .listpage {
    min-height: 500px;
    background: #fff;
    margin: 0 auto;
    margin-bottom: 20px;
    padding: 40px 40px 40px 40px;
    width: 90%;
  }
</style>



